.row {
  color: white;
  margin: 2.4vw 0;
  width: 100%;
}

.row h2 {
  margin-left: 4%;
  font-size: 1.4vw;
}

.row_wrapper {
  position: relative;
  margin: 2vw 0;
  border-radius: 0.4vw;
}

.slider_arrow {
  background-color: rgb(22, 22, 22, 0.5);
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  cursor: pointer;
  z-index: 99;
  opacity: 0;
}

.left {
  left: 0;
}

.right {
  right: 0;
}

.row_posters {
  display: flex;
  margin-left: 4vw;
  width: max-content;
  transition: all 0.2s ease;
  overflow: hidden;
  overflow-x: scroll;
}

.row_posters::-webkit-scrollbar {
  display: none;
}

.row_wrapper:hover .slider_arrow {
  opacity: 1;
}

.row_poster {
  width: calc(120px * 16 / 9);
  height: 120px;
  position: relative;
  margin-right: 5px;
}

.row_poster_image {
  width: calc(120px * 16 / 9);
  height: 120px;
  border-radius: 0.4vw;
}

.row_poster_logo {
  position: absolute;
  max-width: 70%;
  max-height: 30%;
  left: 0;
  right: 0;
  top: 40%;
  margin-left: auto;
  margin-right: auto;
  filter: drop-shadow(5px 5px 5px #222);
}

.row_posterLarge {
  max-height: 250px;
}

.row_posterLarge:hover {
  transform: scale(1.09);
}
